import { createSlice } from "@reduxjs/toolkit";

const name = 'counter';

const initialState = {
    counter: 16
}

const reducers = {
    increment: (state) => { state.counter++ },
    decrement: (state) => { state.counter-- },
    reset: (state) => { state.counter = 16 },
    update: (state, { payload }) => { state.counter += payload },
}

const init = { name, initialState, reducers }

const counterSlice = createSlice(init);

// State
export default counterSlice.reducer;

// functions
export const { increment, decrement, reset, update } = counterSlice.actions;
