// Pages
import Home from './Home'

import About from './about'
import History from './about/History'
import Mission from './about/Mission'
import Vission from './about/Vission'

import Products from './products'
import Product from './products/Product'

import Services from './Services'

import Users from './users'
import User from './users/User'
import Branch from './users/Branch'

import Login from './Login'

import P404 from './P404'


// React router DOM
import { Route, Routes } from 'react-router-dom'

// Redux
import { useSelector } from 'react-redux';

export default function Pages() {

    const { loggedin } = useSelector(store => store.userSlice)

    return (
        <>
            <Routes>
                <Route path='/' element={<Home />} />

                {/* About */}
                <Route path='about' element={<About />} >
                    <Route path='history' element={<History />} />
                    <Route path='mission' element={<Mission />} />
                    <Route path='vission' element={<Vission />} />
                </Route>

                {
                    loggedin &&
                    <>
                        {/* Products */}
                        <Route path='products' element={<Products />} />
                        <Route path='products/:id' element={<Product />} />

                        {/* Users */}
                        <Route path='users' element={<Users />} />
                        <Route path='users/:id' element={<User />} />
                        <Route path='users/branch/:branch_name' element={<Branch />} />
                        <Route path='users/branch/:branch_id/:id' element={<User />} />

                        <Route path='services' element={<Services />} />
                    </>

                }

                {
                    !loggedin &&
                    <Route path='login' element={<Login />} />
                }

                <Route path='*' element={<P404 />} />
            </Routes>
        </>
    )
}
