import { useState, useEffect } from 'react';

import { useParams } from 'react-router-dom'


export default function Product() {

  // 1 - get product id
  const { id } = useParams();

  // 2 -define an empty product
  const [product, setProduct] = useState({});

  console.log('component rendered')

  // 4 - Call useEffect Hook
  // useEffect( ()=> {} , [] );
  useEffect(() => {
    console.log('useEffect called')

    // A - Call the get_product() function once
    get_product();
  }, []);


  // 3 create an async function to fetch the data and update the useState product
  const get_product = async () => {
    console.log('get_product called')

    const product_api = 'https://dummyjson.com/products/' + id;
    
    // A = fetch data
    const res = await fetch(product_api);
    
    console.log(res);
    console.log(product);
    
    // B = call SetProduct
    setProduct(await res.json());

    console.log(product);
  }

  return (
    <div>Product {id}
      <p>{product.title}</p>
      <img src={product.thumbnail} />
    </div>
  )
}
