import { NavLink, Link, useNavigate } from 'react-router-dom'

// redux
import { useSelector, useDispatch } from 'react-redux'
import { logout } from '../redux/userSlice';

export default function Nav() {

    const { loggedin } = useSelector(store => store.userSlice);

    const dispatch = useDispatch();

    const navigate = useNavigate();
 
    const handle_logout = () => {
        // navigate('/');
        dispatch(logout());

        localStorage.clear();
        sessionStorage.clear();
     }

    return (
        <nav className='flex gap-4 justify-between bg-slate-900'>
            <div>

                <NavLink className={({ isActive }) => isActive ? styles.active_link : styles.link} to="/">Home</NavLink>
                <NavLink className={({ isActive }) => isActive ? styles.active_link : styles.link} to="about">About</NavLink>
                {
                    loggedin &&
                    <>
                        <NavLink className={({ isActive }) => isActive ? styles.active_link : styles.link} to="products">Products</NavLink>
                        <NavLink className={({ isActive }) => isActive ? styles.active_link : styles.link} to="services">Services</NavLink>
                        <NavLink className={({ isActive }) => isActive ? styles.active_link : styles.link} to="users">Users</NavLink>
                    </>
                }
                <NavLink className={({ isActive }) => isActive ? styles.active_link : styles.link} to="contacts">Contact us</NavLink>
            </div>

            <div className='text-white px-3 flex gap-3'>
                {
                    loggedin ?
                        <Link className='px-3 py-1 text-white bg-red-800' onClick={handle_logout}>Logout</Link> :
                        <>
                            <Link className='px-3 py-1 text-white bg-sky-800'>Register</Link>
                            <Link to='login' className='px-3 py-1 text-white bg-green-800'>Login</Link>
                        </>
                }
            </div>


        </nav>
    )
}


const styles = {
    link: 'text-slate-200 hover:bg-slate-700 p-4',
    active_link: 'text-green-500  hover:bg-slate-700 p-4'
}

