import { createSlice } from '@reduxjs/toolkit'

const name = 'settings';

const initialState = {
    darkMode: false,
    language: 'en'
};

const reducers = {
    changeMode: state => { state.darkMode = !state.darkMode },
    changeLang: (state, { payload }) => { state.language = payload }
}

const init = { name, initialState, reducers }

const settingsSlice = createSlice(init);


// export state
export default settingsSlice.reducer;

// export function

export const { changeMode, changeLang } = settingsSlice.actions;