import { Link } from "react-router-dom";

const products_api = 'https://dummyjson.com/products';


const res = await fetch(products_api);

console.log(res);

const { products } = await res.json();

console.log(products);


export default function Products() {

  const show_product = (product, i) => (
    <div key={i} className="w-full max-w-[300px] border-4 flex flex-col justify-between gap-4 shadow-md rounded-lg overflow-hidden">
      <h2 className="text-2xl text-center">{product.title}</h2>

      <div className="h-[300px] overflow-hidden">
        <img className="w-100" src={product.thumbnail} />
      </div>

      <Link to={`${product.id}`} className="bg-green-600 text-green-100 text-center p-4" >Details</Link>
    </div>)


  return (
    <div>
      <h1 className="text-6xl mb-5">All Products</h1>
      <div className="flex flex-wrap justify-center gap-5">

        {
          products.map(show_product)
        }

      </div>

    </div>
  )
}
