import { createSlice } from '@reduxjs/toolkit'

const name = 'user';

const initialState = {
    id: null,
    firstName: null,
    lastName: null,
    email: null,
    gender: null,
    image: null,
    token: null,
    loggedin: false,
};

const reducers = {
    login: (state,
        { payload:
            { id, firstName, lastName, email, gender, image, token } }
    ) => {
        state.id = id;
        state.firstName = firstName;
        state.lastName = lastName;
        state.email = email;
        state.gender = gender;
        state.image = image;
        state.token = token;
        state.loggedin = true;
    },
    logout: state => {
        state.id = null;
        state.firstName = null;
        state.lastName = null;
        state.email = null;
        state.gender = null;
        state.image = null;
        state.token = null;
        state.loggedin = false;
    }
}

const init = { name, initialState, reducers };

const userSlice = createSlice(init);


// exports

// export state
export default userSlice.reducer;

// export actions

export const { login, logout } = userSlice.actions;