import { Link } from 'react-router-dom'

export default function P404() {
    return (
        <div>
            <p className='p-5 border-l-8 border-l-red-800 bg-red-200 text-red-800'>The page you search for is not found</p>

            <p className='p-3 my-2'>
                <a className='text-red-900' href="/">Go to home page - (HTML)</a>
            </p>

            <p className='p-3 my-2'>
                <Link className='text-red-900' to="/">Go to home page - (React Router DOM)</Link>
            </p>
        </div>
    )
}
