// Components
import Header from './components/Header'
import Footer from './components/Footer'
import Nav from './components/Nav'

// Pages
import Pages from './pages' // same as import Pages from './pages/index.jsx

// react-toastify
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

// redux
import { login } from './redux/userSlice';
import { useDispatch } from 'react-redux';

export default function App() {

    // Checks if the user is loggedin in the localStorage

    const dispatch = useDispatch();

    const { user_data } = localStorage;
    const { user_data_temp } = sessionStorage;


    console.log(user_data)
    
    if (user_data) {
        const data = JSON.parse(user_data);

        console.log(data);
        
        dispatch(login(data));
        
    }
    
    if ( user_data_temp ) {
        const data = JSON.parse(user_data_temp);
    
        console.log(data);
        
        dispatch(login(data));

    }

    return (
        <>

            <ToastContainer
                position="top-center"
                autoClose={15000}
                hideProgressBar={true}
                newestOnTop={true}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="dark" />

            <Header />

            <Nav />

            <Pages />

            <Footer />
        </>
    )
}

