// Redux
import { useSelector, useDispatch } from 'react-redux';
import { increment, decrement, update, reset } from '../redux/counterSlice';

export default function Counter() {

  const { counter } = useSelector(store => store.counterSlice)

  const dispatch = useDispatch();


  return (
    <div>
      <div className={styles.container}>
        <button className={`bg-green-500 ${styles.btn}`} onClick={() => dispatch(increment())}>+</button>

        <span className={styles.number}>  {counter} </span>

        <button className={`${styles.btn} ${styles.red_btn}`} onClick={() => dispatch(decrement())}>-</button>
      </div>
      
      <div className={styles.container}>
        <button className={`bg-sky-500 ${styles.btn}`} onClick={() => dispatch(update(5))}>+5</button>
        <button className={`bg-pink-500 ${styles.btn}`} onClick={() => dispatch(update(10))}>+10</button>
      </div>
      
      <div className={styles.container}>
        <button className={`bg-yellow-300 ${styles.btn}`} onClick={() => dispatch(reset())}>Reset</button>
      </div>

    </div>
  )
}

const styles = {
  container: 'my-5 flex justify-center items-center gap-4',
  btn: 'px-2 py-1 rounded-md text-2xl border w-12 h-12',
  green_btn: 'bg-green-500 text-green-100',
  red_btn: 'bg-red-500 text-red-100',
  number: 'text-2xl p-4'
}
