import { useState } from "react"

// Redux
import { login } from '../redux/userSlice';
import { useDispatch } from 'react-redux';

// React Router DOM
import { useNavigate } from 'react-router-dom'

export default function Login() {

    const [username, setUsername] = useState('kmeus4');
    const [password, setPassword] = useState('aUTdmmmbH');
    const [remember, setRemember] = useState(false);
    const [errorMsg, setErrorMsg] = useState('');

    const dispatch = useDispatch();
    const navigate = useNavigate();

    const handle_login = async () => {
        console.log(username);
        console.log(password);
        console.log(remember);


        const login_api = 'https://dummyjson.com/auth/login';

        const login_data = {
            username,
            password
        }

        const login_data_json = JSON.stringify(login_data);

        console.log(login_data);
        console.log(login_data_json);

        const fetch_req = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: login_data_json
        }

        const res = await fetch(login_api, fetch_req);

        if (res.status === 400) {
            // Login error
            setErrorMsg("Wrong username and password!!!")
        } else {
            setErrorMsg('');

            const data = await res.json();
            console.log(data);

            // Update Redux
            dispatch(login(data));

            const user_data_json = JSON.stringify(data);

            if (remember) {

                // Update localStorage
                // localStorage.setItem('user_data', user_data_json);
                localStorage.user_data = user_data_json;
            } else {
                // Update sessionStorage
                sessionStorage.user_data_temp = user_data_json;
            }


            navigate('/');


        }


    }


    return (
        <div className="h-100 m-5 p-5 border shadow-md rounded-md">
            <h1 className="text-center text-salte-700 mb-4 text-3xl font-extrabold">LOGIN</h1>
            <div className="flex flex-col gap-3">
                <label className="flex flex-col gap-2 text-2xl   p-4">
                    Username:
                    <input className="border-b py-3 outline-none" type="text" value={username} placeholder="Your username" onChange={e => setUsername(e.target.value)} />
                </label>
                <label className="flex flex-col gap-2 text-2xl   p-4">
                    Password:
                    <input className="border-b py-3 outline-none" type="password" value={password} placeholder="**********" onChange={e => setPassword(e.target.value)} />
                </label>

                <div>
                    <label>
                        <input type="checkbox" onChange={() => setRemember(!remember)} />
                        Remember Me
                    </label>
                </div>
                <div className="self-end">
                    <button onClick={handle_login} className="px-4 py-2 bg-green-800 text-white rounded-sm shadow-sm">Login</button>
                </div>

                <div>
                    <p className="text-red-700 font-extrabold">{errorMsg}</p>
                </div>
            </div>
        </div>
    )
}